#sidebar {
    /* don't forget to add all the previously mentioned styles here too */
    background: #606060;
    color: #fff;
    transition: all 0.3s;
}

#sidebar .sidebar-header {
    background: #6d7fcc;
    cursor: pointer;
}

#sidebar ul.components {
    padding-bottom: 20px;
    border-bottom: 1px solid #47748b;
}

#sidebar ul p {
    color: #fff;
    padding: 10px;
}

#sidebar ul li a {
    padding: 10px;
    font-size: 1.1em;
    display: block;
}
#sidebar ul li a:hover {
    color: #7386D5;
    background: #fff;
    text-decoration: none;
}

#sidebar ul li.active > a, a[aria-expanded="true"] {
    color: rgb(165, 219, 245);
    background: #000;
    border-left: solid 3px rgb(165, 219, 245);
}
ul ul a {
    font-size: 0.9em !important;
    padding-left: 30px !important;
    background: #fff;
}

.wrapper {
    display: flex;
    align-items: stretch;
}

#sidebar {
    min-width: 250px;
    max-width: 250px;
    min-height: 100vh;
}

#sidebar.active {
    margin-left: -250px;
}

a[data-toggle="collapse"] {
    position: relative;
}

.dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
}

@media (max-width: 768px) {
    #sidebar {
        margin-left: -250px;
    }
    #sidebar.active {
        margin-left: 0;
    }
}

.logo-marca{
    width: 9vh;
    height: auto;
    margin-left: 3vh;
}

.logo-texto {
    width: 8vw;
    margin-left: 1vh;
    height: auto;
}